<template>
  <!-- Popup -->
  <Popup ref="popup" />
  <router-view />
</template>

<script>
import { ethers } from "ethers";
import Popup from "./components/Popup.vue";

export default {
  components: { Popup },

  data() {
    return {
      provider: null,
      signer: null,
    };
  },

  methods: {
    showPopup: function (_title, _text, _timeOut) {
      this.$refs.popup.show(_title, _text, _timeOut);
    },
  },

  mounted: function () {
    const that = this;

    that.provider = new ethers.providers.Web3Provider(window.ethereum, "any");

    that.provider.getNetwork().then((network) => {
      if (network.chainId !== window.netId) {
        that.showPopup(
          "Wrong network!",
          `Please switch to ${window.netName}`,
          0
        );
        window.ethereum
          .request({
            method: "wallet_switchEthereumChain",
            params: [{ chainId: window.netId0x }],
          })
          .catch((err) => {
            // This error code indicates that the chain has not been added to MetaMask
            if (err.code === 4902) {
              that.showPopup(
                `Add ${window.netName} to Metamask`,
                `and then reload this website.`,
                0
              );
              window.ethereum.request({
                method: "wallet_addEthereumChain",
                params: [
                  {
                    chainName: window.netName,
                    chainId: window.netId0x,
                    nativeCurrency: {
                      name: window.ETH,
                      decimals: 18,
                      symbol: window.ETH,
                    },
                    rpcUrls: [window.rpcServer],
                    blockExplorerUrls: [window.blockExplorer],
                  },
                ],
              });
            }
          });
      }
    });

    that.provider.on("network", (n, o) => {
      if (o) {
        window.location.reload();
      } else {
        that.provider.send("eth_requestAccounts", []).then(() => {
          that.signer = that.provider.getSigner();
        });
      }
    });
  },
};
</script>

