<template>
  <!-- blur overlay for popup -->
  <div class="overlay" v-if="visible === true"></div>

  <!-- Popup -->
  <div class="popup" v-if="visible === true">
    <button class="closeBtn" @click="visible = false">x</button>
    <h3>{{ title }}</h3>
    <p>{{ text }}</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      title: "",
      text: "",
      lastTimeout: 0,
      visible: false,
    };
  },

  methods: {
    show(_title, _text, _timeOut) {
      // to be called from parent using refs:
      // <Popup ref="popup" />
      // showPopup: function (_title, _text, _timeOut) {
      //   this.$refs.popup.show(_title, _text, _timeOut);
      // },

      let that = this;
      that.title = _title.toUpperCase();
      that.text = _text.toUpperCase();
      if (that.lastTimeout > 0) {
        clearTimeout(that.lastTimeout);
      }
      if (_timeOut > 0) {
        that.lastTimeout = setTimeout(function () {
          that.visible = false;
        }, _timeOut);
      }
      that.visible = true;
    },
  },
};
</script>

